/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { useState, useEffect } from 'react';
// @mui
import {
  Table,
  TableRow,
  TableBody,
  TableCell,
  Container,
  Typography,
  TableContainer,
  TablePagination,
  Switch,
  Card,
  Alert,
} from '@mui/material';
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import Scrollbar from '../../components/Scrollbar';
// sections
import { HabukhanListHead } from '../../sections/admin/user/list';
import axios from '../../utils/axios';

// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'network', label: 'Network', alignRight: false },
  { id: 'plan_id', label: 'Plan ID', alignRight: false },
];

// ----------------------------------------------------------------------

export default function Network() {
  const { themeStretch } = useSettings();
  const [userList, setUserList] = useState([]);
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState('asc');
  const [orderBy, setOrderBy] = useState('id');
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [dense, Setdense] = useState(false);
  const AccessToken = window.localStorage.getItem('accessToken');

  useEffect(() => {
    axios.get('/api/website/app/network').then((data) => {
      setUserList(data.data?.network);
    });
  }, [AccessToken]);

  const handleRequestSort = (property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - userList.length) : 0;

  function handleChangeDense(event) {
    Setdense(event.target.checked);
  }

  return (
    <Page title="Network IDs Reference">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Network IDs Reference"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Documentation', href: '/documentation/home' },
            { name: 'Network IDs' },
          ]}
        />

        <Alert severity="info" sx={{ mb: 3 }}>
          <Typography variant="body2">
            Use these Network IDs when making API calls for data, airtime, and other network-related services.
          </Typography>
        </Alert>

        <Card>
          <Scrollbar>
            <TableContainer>
              <Table size={dense ? 'small' : 'medium'}>
                <HabukhanListHead
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  onRequestSort={handleRequestSort}
                />

                <TableBody>
                  {userList.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                    const { network, plan_id } = row;
                    return (
                      <TableRow hover key={plan_id} tabIndex={-1}>
                        <TableCell sx={{ display: 'flex', alignItems: 'center' }}>
                          <Typography variant="subtitle2" noWrap>
                            {network}
                          </Typography>
                        </TableCell>
                        <TableCell align="left">
                          <Typography variant="body2" sx={{ fontFamily: 'monospace' }}>
                            {plan_id}
                          </Typography>
                        </TableCell>
                      </TableRow>
                    );
                  })}
                  {emptyRows > 0 && (
                    <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell colSpan={6} />
                    </TableRow>
                  )}
                </TableBody>
              </Table>
            </TableContainer>
          </Scrollbar>

          <FormControlLabel
            control={<Switch checked={dense} onChange={(event) => handleChangeDense(event)} />}
            label="Dense padding"
            sx={{ px: 3, py: 1.5 }}
          />

          <TablePagination
            rowsPerPageOptions={[5, 10, 25, 50]}
            component="div"
            count={userList.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={(e, page) => setPage(page)}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Card>
      </Container>
    </Page>
  );
}
